import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import React, { Component } from "react";
import { connect } from "react-redux";
import { postData } from "../../../actions/action";
import { ADD_RECO_URL, createUrl } from "../../../constants/url.constants";
import RecommendationModel from "../../../models/RecommendationModel";
import "./../Food.css";

class Recommendation extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = { name: "" };
    this.addReco = this.addReco.bind(this);
  }

  addReco() {
    const { name } = this.state;
    const {
      profileObj,
      addRecommendation,
      fetchAllFoodData,
      updateWeek,
      currentWeek
    } = this.props;
    if (name && name.length > 2) {
      let recommendation = new RecommendationModel(
        profileObj.email,
        name,
        new Date()
      );
      addRecommendation(recommendation);
      this.setState({ name: "" });
      updateWeek(currentWeek);
      setTimeout(() => {
        fetchAllFoodData();
      }, 500);
    }
  }

  handleChange($event) {
    this.setState({ name: $event.target.value });
  }

  render() {
    const { name } = this.state;
    const { classes } = this.props;
    return (
      <>
        <Typography variant="caption" className="details-header">
          Please check if already present!
        </Typography>
        <TextField
          id="standard-name"
          label="Food Name"
          placeholder="Min. 3 characters"
          className={classes.textField}
          value={name}
          onChange={this.handleChange}
          margin="normal"
        />
        <Button
          variant="contained"
          color="primary"
          onClick={() => this.addReco()}
        >
          Send
          <i
            style={{ marginLeft: "10px" }}
            className="fa fa-paper-plane"
            aria-hidden="true"
          ></i>
        </Button>
      </>
    );
  }
}

Recommendation.propTypes = {};

function mapStateToProps(state, ownProps) {
  return {};
}

function mapDispatchToProps(dispatch) {
  return {
    addRecommendation: recommendation =>
      dispatch(postData(createUrl(ADD_RECO_URL), recommendation))
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Recommendation);
